import React, { useEffect } from 'react';
import { useLanguage } from '../context/LanguageContext';
import '../styles/Home.css';  // We'll create this next
import ContactSection from '../components/ContactSection';
import Partners from '../components/Partners';
import Mission from '../components/Mission';
import LatestProjects from '../components/LatestProjects';
import Testimonials from '../components/Testimonials';

const Home = () => {
    const { language } = useLanguage();

    const translations = {
        'zh-TW': {
            dream: '夢想',
            home: '家園',
            reality: '成為現實',
            mission: '使命',
            missionText: '我們致力於成為全球房地產領域的信賴夥伴，為客戶提供專業、誠信良質的服務，幫助他們實現家園夢想與投資目標。'
        },
        'en': {
            dream: 'MAKING',
            home: 'DREAMS',
            reality: 'COME TRUE',
            mission: 'MISSION',
            missionText: 'We are committed to becoming a trusted partner in global real estate, providing professional and reliable services to help our clients achieve their dreams of home ownership and investment goals.'
        }
    };

    const t = translations[language];

    useEffect(() => {
        document.body.classList.add('no-padding');
        
        // 處理 URL hash 跳轉
        const hash = window.location.hash;
        if (hash === '#contact-section') {
            const contactSection = document.getElementById('contact-section');
            if (contactSection) {
                setTimeout(() => {
                    contactSection.scrollIntoView({ behavior: 'smooth' });
                }, 100);
            }
        }
        
        window.scrollTo({ top: 0, behavior: 'instant' });
        
        return () => {
            document.body.classList.remove('no-padding');
        };
    }, []);

    return (
        <div className="page-container home-page">
            {/* Hero Section */}
            <section className="home-hero">
                <div className="hero-content">
                    <div className="hero-text-group">
                        <span className="hero-text-large">{t.dream}</span>
                        <div className="hero-text-right">
                            <span className="hero-text-small">{t.home}</span>
                            <span className="hero-subtitle">{t.reality}</span>
                        </div>
                    </div>
                </div>
            </section>
            
            <div className="content-container"> {/* 確保所有內容在同一個容器中 */}
                <Mission />
                <LatestProjects />
                <Testimonials />
                <Partners />
                
                {/* 免責聲明區塊 */}
                <section className="disclaimer-section">
                    <div className="disclaimer-container">
                        <h3>免責聲明</h3>
                        <div className="disclaimer-content">
                            <p className="main-disclaimer">
                                ※免責聲明：本網頁資訊內容均由開發商/發展商/經紀商所提供，所包含之所有資料，包括文字、圖表、相關網站或其他項目僅供參考用途，並僅作廣告宣傳用途，且僅包含基本信息。本公司在編制網頁資訊內容時，已採取謹慎態度並力求準確，儘管如此，資料內容可能會因開發商/發展商/經紀商的修改而有所變更，本公司保留權利不時或隨時，通知或不通知的情況下，對此網頁所含內容和信息作出修改或改變任何內容。
                            </p>
                            <p className="main-disclaimer">
                                寬鑫國際對上述資料之最新性，可靠性，正確性或完整性不作任何擔保，保證或證明，並明確表明不會對該網頁資料內之任何內容和信息而引起的任何索賠、損失、損害負賠償責任;不對任何第三方的索賠、損失或損害負賠償責任。投資者有責為此網頁所含內容和信息自行進行審慎調查，或向獨立法律或相關專業人士咨詢意見，並在此告知投資者任何投資皆涉及商業風險。
                            </p>
                            <div className="risk-warnings">
                                <p>任何形式的投資均須承擔風險，包括損失全部投資本金的風險，此類投資活動不適合所有人群。</p>
                                <p>本投資為海外投資，而海外投資須承擔額外的金融，監管和法律風險，建議投資者事先對投資進行必要的調查研究。</p>
                            </div>
                        </div>
                    </div>
                </section>
                
                <ContactSection />
            </div>
        </div>
    );
};

export default Home;
