import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import { useLanguage } from '../context/LanguageContext';
import RegistrationModal from '../components/RegistrationModal';
import '../styles/EventDetail.css';
import { getImageUrl } from '../utils/imageUtils';

// 將文本配置抽離
const text = {
    'zh-TW': {
        loading: '載入中...',
        register: '立即報名',
        maxParticipants: '名額上限',
        remainingSpots: '剩餘名額',
        eventDate: '活動日期',
        location: '活動地點',
        sessions: '場次資訊',
        error: '載入失敗，請稍後再試',
        status: {
            upcoming: '即將舉行',
            ongoing: '進行中',
            ended: '已結束'
        }
    },
    'en': {
        loading: 'Loading...',
        register: 'Register Now',
        maxParticipants: 'Max Participants',
        remainingSpots: 'Remaining Spots',
        eventDate: 'Event Date',
        location: 'Location',
        sessions: 'Sessions',
        error: 'Failed to load, please try again later',
        status: {
            upcoming: 'Upcoming',
            ongoing: 'Ongoing',
            ended: 'Ended'
        }
    }
};

const EventDetail = () => {
    const { id } = useParams();
    const { language } = useLanguage();
    const [event, setEvent] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedSessionIndex, setSelectedSessionIndex] = useState(null);

    const currentText = useMemo(() => text[language], [language]);

    const formatDateTime = useCallback((dateTime) => {
        return new Date(dateTime).toLocaleString(
            language === 'zh-TW' ? 'zh-TW' : 'en-US',
            { 
                year: 'numeric',
                month: 'long',
                day: 'numeric',
                hour: '2-digit',
                minute: '2-digit'
            }
        );
    }, [language]);

    const getRegistrationsForSession = useCallback((sessionCode) => {
        return event?.attributes?.registrations?.data?.filter(reg => 
            reg.attributes.sessionCode === sessionCode
        )?.length || 0;
    }, [event]);

    const handleRegistrationClick = (sessionIndex) => {
        setSelectedSessionIndex(sessionIndex);
        setIsModalOpen(true);
    };

    const handleRegistrationSuccess = () => {
        // Refresh the event data to update registration counts
        fetchEvent();
    };

    const fetchEvent = async () => {
        try {
            setLoading(true);
            setError(null);
            const response = await fetch(`http://localhost:1339/api/events/${id}?populate=*`);
            if (!response.ok) {
                throw new Error('Failed to fetch event data');
            }
            const data = await response.json();
            setEvent(data.data);
        } catch (error) {
            console.error('Error fetching event:', error);
            setError(error.message);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchEvent();
    }, [id]);

    if (loading) {
        return (
            <div className="loading-container">
                <div className="loading-spinner"></div>
                <p>{currentText.loading}</p>
            </div>
        );
    }

    if (error) {
        return (
            <div className="error-container">
                <p className="error-message">{currentText.error}</p>
                <button onClick={() => window.location.reload()} className="retry-button">
                    重試
                </button>
            </div>
        );
    }

    if (!event) return null;

    return (
        <div className="event-detail-page">
            <div className="event-detail-header">
                <div className="header-image">
                    {event.attributes.coverImage?.data && (
                        <img 
                            src={getImageUrl(event.attributes.coverImage)}
                            alt={event.attributes.title}
                            loading="lazy"
                        />
                    )}
                </div>
                <div className="header-content">
                    <span className={`event-status ${event.attributes.status}`}>
                        {currentText.status[event.attributes.status]}
                    </span>
                    <h1>{event.attributes.title}</h1>
                    <p className="event-description">{event.attributes.description}</p>
                </div>
            </div>

            <div className="event-sessions">
                <h2>{currentText.sessions}</h2>
                <div className="sessions-grid">
                    {event.attributes.session?.map((session, index) => {
                        const registrations = getRegistrationsForSession(session.sessionCode);
                        const isDisabled = event.attributes.status === 'ended' || 
                                        session.maxParticipants <= registrations;
                        
                        return (
                            <div key={index} className="session-card">
                                <div className="session-header">
                                    <div className="session-time">
                                        <i className="far fa-calendar-alt"></i>
                                        <div className="time-details">
                                            <span>{formatDateTime(session.startDateTime)}</span>
                                            <span>-</span>
                                            <span>{formatDateTime(session.endDateTime)}</span>
                                        </div>
                                    </div>
                                    <div className="session-location">
                                        <i className="fas fa-map-marker-alt"></i>
                                        <span>{session.location}</span>
                                    </div>
                                </div>
                                <div className="capacity-info">
                                    <span className="max-participants">
                                        <i className="fas fa-users"></i>
                                        {currentText.maxParticipants}: {session.maxParticipants}
                                    </span>
                                    <span className="remaining">
                                        {currentText.remainingSpots}: {
                                            session.maxParticipants - registrations
                                        }
                                    </span>
                                </div>
                                <button 
                                    className={`register-button ${isDisabled ? 'disabled' : ''}`}
                                    disabled={isDisabled}
                                    onClick={() => handleRegistrationClick(index)}
                                >
                                    {currentText.register}
                                </button>
                            </div>
                        );
                    })}
                </div>
            </div>

            <RegistrationModal
                isOpen={isModalOpen}
                onClose={() => setIsModalOpen(false)}
                eventId={id}
                sessionIndex={selectedSessionIndex}
                onSuccess={handleRegistrationSuccess}
            />
        </div>
    );
};

export default EventDetail; 