import React, { useState } from 'react';
import { useLanguage } from '../context/LanguageContext';
import '../styles/RegistrationModal.css';

const text = {
    'zh-TW': {
        title: '活動報名',
        name: '姓名',
        phone: '電話',
        email: '電子郵件',
        message: '留言',
        submit: '確認報名',
        cancel: '取消',
        namePlaceholder: '請輸入姓名',
        phonePlaceholder: '請輸入電話',
        emailPlaceholder: '請輸入電子郵件',
        messagePlaceholder: '有什麼想告訴我們的嗎？',
        required: '必填',
        invalidEmail: '請輸入有效的電子郵件',
        invalidPhone: '請輸入有效的電話號碼',
        success: '報名成功！',
        error: '報名失敗，請稍後再試'
    },
    'en': {
        title: 'Event Registration',
        name: 'Name',
        phone: 'Phone',
        email: 'Email',
        message: 'Message',
        submit: 'Register',
        cancel: 'Cancel',
        namePlaceholder: 'Enter your name',
        phonePlaceholder: 'Enter your phone number',
        emailPlaceholder: 'Enter your email',
        messagePlaceholder: 'Anything you want to tell us?',
        required: 'Required',
        invalidEmail: 'Please enter a valid email',
        invalidPhone: 'Please enter a valid phone number',
        success: 'Registration successful!',
        error: 'Registration failed, please try again later'
    }
};

// Strapi Cloud API 端點
const API_URL = process.env.REACT_APP_API_URL || 'https://your-strapi-cloud-url.com/api';

const RegistrationModal = ({ isOpen, onClose, eventId, sessionIndex, onSuccess }) => {
    const { language } = useLanguage();
    const currentText = text[language];

    const [formData, setFormData] = useState({
        name: '',
        phone: '',
        email: '',
        message: ''
    });

    const [errors, setErrors] = useState({});
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [submitStatus, setSubmitStatus] = useState(null); // 'success', 'error', or null

    const validateForm = () => {
        const newErrors = {};
        
        if (!formData.name.trim()) {
            newErrors.name = `${currentText.name} ${currentText.required}`;
        }
        
        if (!formData.phone.trim()) {
            newErrors.phone = `${currentText.phone} ${currentText.required}`;
        } else if (!/^[0-9]{10}$/.test(formData.phone.replace(/[- ]/g, ''))) {
            newErrors.phone = currentText.invalidPhone;
        }
        
        if (!formData.email.trim()) {
            newErrors.email = `${currentText.email} ${currentText.required}`;
        } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
            newErrors.email = currentText.invalidEmail;
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        if (!validateForm()) {
            return;
        }

        setIsSubmitting(true);
        setSubmitStatus(null);

        try {
            const response = await fetch(`${API_URL}/registrations`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    data: {
                        name: formData.name,
                        phone: formData.phone,
                        email: formData.email,
                        message: formData.message,
                        sessionIndex: sessionIndex,
                        status: 'pending',
                        event: eventId
                    }
                })
            });

            if (!response.ok) {
                const errorData = await response.json();
                console.error('Registration error details:', errorData);
                throw new Error('Registration failed');
            }

            setSubmitStatus('success');
            onSuccess();
            
            // 清空表單
            setFormData({
                name: '',
                phone: '',
                email: '',
                message: ''
            });
            
            // 顯示成功訊息，然後關閉 modal
            setTimeout(() => {
                onClose();
            }, 2000);
            
        } catch (error) {
            console.error('Error submitting registration:', error);
            setSubmitStatus('error');
        } finally {
            setIsSubmitting(false);
        }
    };

    if (!isOpen) return null;

    return (
        <div className="modal-overlay">
            <div className="modal-content">
                <h2>{currentText.title}</h2>
                
                {submitStatus === 'success' && (
                    <div className="success-message">
                        <i className="fas fa-check-circle"></i>
                        <p>{currentText.success}</p>
                    </div>
                )}
                
                {submitStatus === 'error' && (
                    <div className="error-message">
                        <i className="fas fa-exclamation-circle"></i>
                        <p>{currentText.error}</p>
                    </div>
                )}
                
                {submitStatus !== 'success' && (
                    <form onSubmit={handleSubmit}>
                        <div className="form-group">
                            <label htmlFor="name">{currentText.name}</label>
                            <input
                                type="text"
                                id="name"
                                value={formData.name}
                                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                                placeholder={currentText.namePlaceholder}
                            />
                            {errors.name && <span className="error">{errors.name}</span>}
                        </div>

                        <div className="form-group">
                            <label htmlFor="phone">{currentText.phone}</label>
                            <input
                                type="tel"
                                id="phone"
                                value={formData.phone}
                                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                                placeholder={currentText.phonePlaceholder}
                            />
                            {errors.phone && <span className="error">{errors.phone}</span>}
                        </div>

                        <div className="form-group">
                            <label htmlFor="email">{currentText.email}</label>
                            <input
                                type="email"
                                id="email"
                                value={formData.email}
                                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                                placeholder={currentText.emailPlaceholder}
                            />
                            {errors.email && <span className="error">{errors.email}</span>}
                        </div>

                        <div className="form-group">
                            <label htmlFor="message">{currentText.message}</label>
                            <textarea
                                id="message"
                                value={formData.message}
                                onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                                placeholder={currentText.messagePlaceholder}
                            />
                        </div>

                        <div className="modal-actions">
                            <button 
                                type="button" 
                                onClick={onClose} 
                                className="cancel-button"
                                disabled={isSubmitting}
                            >
                                {currentText.cancel}
                            </button>
                            <button 
                                type="submit" 
                                className="submit-button"
                                disabled={isSubmitting}
                            >
                                {isSubmitting ? (
                                    <span className="loading-text">
                                        <i className="fas fa-spinner fa-spin"></i> 處理中...
                                    </span>
                                ) : (
                                    currentText.submit
                                )}
                            </button>
                        </div>
                    </form>
                )}
            </div>
        </div>
    );
};

export default RegistrationModal; 